				"html": "<h1 id=\"a-little-about-me\"><a href=\"#a-little-about-me\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>A little about me</h1>\n<p>I'm Sartios, a 29 year old software developer from Greece.</p>\n<p>Building software is my passion and profession. So far I have been working with various programming languages, technologies and architectures. I had learned programming with C++ and Java, and have worked in Java, C# and JavaScript production applications.</p>\n<p>Working on both front-end and back-end parts of an application is valuable for me. Having a comprehensive overview of the product helps me deliver quality services.</p>\n<p>Open source software is very important for me. I learn by studying others code and often contribute to open issues.</p>\n<p>I believe that sharing and documenting knowledge is an effective way to give back to the community and this is the concept behind this blog.</p>"
